@import url(https://fonts.googleapis.com/css?family=Life+Savers|Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Life+Savers&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.landing-page {
  height: 100vh;
  background-color: #40404A;
  display: flex; }

.hero-section {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center; }

.main-title {
  color: #F07B3B;
  font-family: 'Life Savers', cursive;
  font-size: 100px;
  margin: 0; }

.main-tagline {
  color: aqua;
  font-family: 'Life Savers', cursive;
  font-size: 40px;
  margin: 0; }

.lightbulb-section {
  width: 50%;
  display: flex; }

.landing-header {
  height: 25%;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around; }

.mission-article {
  height: 70%;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-radius: 20px;
  background-color: #FAFAFA; }

.purpose {
  padding: 20px;
  color: #800080;
  font-family: 'Roboto', sans-serif;
  font-size: 25px;
  font-weight: bold;
  letter-spacing: 1.5px;
  line-height: 1.3;
  text-align: center; }

.wordcloud-container {
  width: 60%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #B2B5B5;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px; }

.mission-statement {
  width: 70%; }

.wordcloud-man {
  width: unset;
  height: 400px; }

.bulb {
  width: 175px;
  fill: #FAFAFA; }

.bulb-one {
  height: 400px; }

.bulb-two {
  height: 600px; }

.bulb-three {
  height: 300px; }

.bulb-four {
  height: 500px; }

.bulb-link {
  position: relative; }

.bulb-div {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  color: #800080;
  font-family: 'Life Savers', cursive;
  font-weight: bold;
  text-decoration: none; }

.bulb-div:hover {
  background-color: #fff;
  -webkit-animation: neon3 1.5s ease-in-out infinite alternate;
          animation: neon3 1.5s ease-in-out infinite alternate; }

@-webkit-keyframes neon3 {
  from {
    box-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #FFDD1B, 0 0 70px #FFDD1B, 0 0 80px #FFDD1B, 0 0 100px #FFDD1B, 0 0 150px #FFDD1B; }
  to {
    box-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #FFDD1B, 0 0 35px #FFDD1B, 0 0 40px #FFDD1B, 0 0 50px #FFDD1B, 0 0 75px #FFDD1B; } }

@keyframes neon3 {
  from {
    box-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #FFDD1B, 0 0 70px #FFDD1B, 0 0 80px #FFDD1B, 0 0 100px #FFDD1B, 0 0 150px #FFDD1B; }
  to {
    box-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #FFDD1B, 0 0 35px #FFDD1B, 0 0 40px #FFDD1B, 0 0 50px #FFDD1B, 0 0 75px #FFDD1B; } }

.login {
  width: 90px;
  height: 90px;
  top: 207px;
  left: 42px;
  font-size: 33px; }

.sign {
  width: 120px;
  height: 120px;
  top: 276px;
  left: 27px;
  font-size: 35px; }

.personalities {
  width: 170px;
  height: 170px;
  top: 408px;
  font-size: 28px;
  left: 2px; }

.assessments {
  width: 150px;
  height: 150px;
  top: 346px;
  font-size: 26px;
  left: 12px; }

.header {
  height: 200px;
  display: flex;
  justify-content: space-between;
  background-color: #FAFAFA;
  font-family: 'Life Savers', cursive; }

.header-text {
  display: flex;
  flex-direction: column; }

.home-page {
  cursor: pointer;
  text-decoration: none; }

.app-title,
.app-tagline {
  margin-left: 25px; }

.app-title {
  margin: 0 0 20px 20px;
  padding-top: 25px;
  color: #F07B3B;
  font-size: 75px;
  font-weight: bold; }

.app-tagline {
  margin-top: 0;
  color: #800080;
  font-size: 32px; }

.lightbulb-container {
  width: 50%;
  float: right; }

.lightbulbs {
  height: 200px;
  width: 50%;
  float: right; }

.login-container {
  height: 75vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #40404A; }

.input-container {
  width: 60%;
  height: 80%;
  display: flex;
  background-color: #FAFAFA;
  border-radius: 20px; }

.left-container {
  height: 100%;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center; }

.login-text {
  margin: 50px 0;
  color: #5D6675;
  font-family: 'Life Savers', cursive; }

.email-input,
.password-input,
.sign-in-btn {
  height: 50px;
  width: 80%;
  padding: 10px;
  border: 1px solid #757575;
  color: #40404A;
  font-family: 'Open Sans', sans-serif;
  font-size: 20px; }

.sign-in-btn {
  background-color: #800080;
  color: #fff;
  border: none;
  cursor: pointer;
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  outline: none; }

.sign-in-btn:disabled {
  opacity: 0.2; }

.password-input {
  margin: 25px 0; }

.sign-up-text {
  display: flex;
  flex-direction: column;
  margin: 50px 0;
  color: #66D9EF;
  font-family: 'Life Savers', cursive;
  text-align: center; }

.right-container {
  height: 100%;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center; }

.signup-link {
  color: #F07B3B; }

.facebook-btn {
  height: 50px;
  width: 80%;
  padding: 15px;
  border: none;
  color: #fff;
  cursor: pointer;
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
  font-weight: bold;
  line-height: 50px;
  text-decoration: none;
  text-align: center; }

.facebook-btn {
  background-color: #4761A5; }

.container {
  height: 80vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #40404A; }

.signup-container {
  width: 60%;
  height: 80%;
  display: flex;
  background-color: #FAFAFA;
  border-radius: 20px; }

.create-user-form {
  height: 100%;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }

.create-account-heading,
.social-media-heading {
  color: #5D6675;
  font-family: 'Life Savers', cursive;
  font-size: 25px; }

.social-media-heading {
  margin-top: 20px;
  margin-bottom: 50px; }

.newuser-name,
.newuser-email,
.newuser-password,
.signup-btn {
  width: 80%;
  height: 40px;
  padding: 20px;
  color: #40404A;
  font-family: 'Open Sans', sans-serif;
  font-size: 20px; }

.social-media-container {
  height: 100%;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center; }

.social-media-buttons {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center; }

.signup-btn {
  padding: 0;
  background-color: #800080;
  color: #fff;
  border: none;
  cursor: pointer;
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  outline: none; }

.signup-btn:disabled {
  opacity: 0.2; }


